html,body {
    height: 100%;
}

#root {
    font-family: Fontin-Sans;
    min-height: calc(100% - 50px);
}

.brand-image {
    padding-bottom: 0px;
}

.footer {
    font-family: Fontin-Sans;
    bottom: 0%;
    color: #9d9d9d;
    background-color: rgb(36, 53, 90);
    text-align: center;
    width: 100%;
    height: 50px;
}

.footer-content {
    display: inline-block;
    padding: 15px 0;
    font-weight: bold;
}

.navbar {
    background-color: rgb(36, 53, 90);
    border: none;
}


